import styled from 'styled-components';

import publications from '../../../../data/publications.json';
import { Publication } from '../../../../data/interfaces';
import { Divider, List, Space, Tag, Typography } from 'antd';
import Markdown from 'react-markdown';
import Link from 'antd/es/typography/Link';
const { Title, Text } = Typography;



const ResearchViewContainer = styled.div`
   margin-left: 48px;
   width: 100%;
`

const ResearchListItem = styled.div`
margin-top: 12px;
p{
   margin: 0px;
}
`

const ConferenceTag = styled(Tag)`
   margin-top: 18px;  
   min-width: 80px;
   text-align: center;
   font-size: 1.2em;
   background-color: #8AC7DB;
   color: #1E5162;
`

const DescriptionSpace = styled(Space)`
   gap: 24px;  
   height: 16px;
`


function ResearchView() {
   const publicationsData = publications as Publication[];
   const groupedPublications = publicationsData.reduce((groups: { [key: number]: [Publication] }, publication) => {
      const groupBy = publication.year;
      const group = (groups[groupBy] || []);
      group.push(publication);
      groups[groupBy] = group;
      return groups;
   }, {});

   return (
      <ResearchViewContainer>
         <Space direction='vertical' style={{ width: '100%' }}>
            <Text strong style={{ fontSize: '1.5em' }} >Selected publications</Text>
            {
               Object.entries(groupedPublications).reverse().map(([year, yearPublications]) => <div>
                  <Divider key={`divider` + year} orientation="left" orientationMargin="0"><Text strong>{year}</Text></Divider>
                  <List
                     key={`list` + year}
                     itemLayout="vertical"
                     size="default"
                     dataSource={yearPublications}
                     renderItem={(publication) => (
                        <ResearchListItem key={publication.title}>
                           <Space size={24} direction='horizontal'>
                              <ConferenceTag>{publication.conference}</ConferenceTag>
                              <Title level={3}>{publication.title}</Title>
                           </Space>
                           <br />
                           <DescriptionSpace direction='horizontal'>
                              <ConferenceTag style={{ visibility: 'hidden' }}>{publication.conference}</ConferenceTag>
                              <Markdown>{publication.authors}</Markdown>
                           </DescriptionSpace>
                           <br />
                           <DescriptionSpace direction='horizontal'>
                              <ConferenceTag style={{ visibility: 'hidden' }}>{publication.conference}</ConferenceTag>
                              <Text italic>{publication.proceedings} ({publication.year})</Text>
                           </DescriptionSpace>
                           <br />
                           <DescriptionSpace direction='horizontal'>
                              <ConferenceTag style={{ visibility: 'hidden' }}>{publication.conference}</ConferenceTag>
                              <Space size={8} style={{ height: '16px' }} direction='horizontal'>
                                 {
                                    publication.links !== undefined ? publication.links.map((link) => <Link href={link.url} target="_blank">{link.name}</Link>) : null
                                 }
                              </Space>
                           </DescriptionSpace>
                        </ResearchListItem>
                     )}
                  />
               </div>)
            }
         </Space>
      </ResearchViewContainer>
   );
}

export default ResearchView;
