import { ConfigProvider, Menu, MenuProps, Space } from 'antd';
import styled from 'styled-components';
import CV from '../../../assets/CV_Witold_Kosciukiewicz.pdf';
import ExperienceView from './experience/ExperienceView';
import ProjectsView from './projects/ProjectsView';
import ResearchView from './research/ResearchView';
import PostView from './post/PostView';
import { Route, Routes, useNavigate } from 'react-router-dom';

const RouterViewContainer = styled.div`
  margin-top: 0;
  padding-top: 24px;
  min-height: 100vh;
  width: 100%;
  background-color: ${props => props.theme.mainBackground};
`

const items: MenuProps['items'] = [
   {
      label: 'Experience',
      key: '/experience',
   },
   {
      label: 'Projects',
      key: '/projects',
   },
   {
      label: 'Research',
      key: '/research',
   },
   {
      label: 'CV',
      key: '/cv',
   },
   {
      label: '🐈‍⬛',
      key: '/leo',
   },
]

function getSelectedMenuItem(): string {
   const pathname = window.location.hash.slice(1);;
   console.log(pathname)
   if (pathname.includes('/projects')) {
      return '/projects'
   }

   return pathname
}

function RouterView() {
   const navigate = useNavigate();
   const onClick: MenuProps['onClick'] = e => {
      if (e.key == '/cv') {
         window.open(CV);
      } else {
         navigate(e.key, { replace: true });
      }
   };


   return (
      <RouterViewContainer>
         <ConfigProvider
            theme={{
               components: {
                  Menu: {
                     colorPrimary: '#000000',
                     groupTitleLineHeight: 2,
                     horizontalLineHeight: 4,
                     activeBarHeight: 0,
                     activeBarWidth: 0,
                     itemColor: '#808080',
                     fontSize: 20,
                  },
               },
            }}
         >
            <Routes>
               <Route path="/projects/:post" element={<PostView />} />
               <Route
                  path="*"
                  element={
                     <Space size={24} direction='vertical'>
                        <ExperienceView />
                        <ProjectsView />
                        <ResearchView />
                     </Space>
                  }
               />
            </Routes>
         </ConfigProvider>
      </RouterViewContainer>
   );
}

export default RouterView;
